import React from 'react';
import {Component} from 'react';
import '../../../styling/slider1.css';
import { Fade } from 'react-slideshow-image';
import { Button ,Col} from 'reactstrap';
const logo = require('../../../images/dayas.jpg');
const logo1 = require('../../../images/dayas2.jpg');
const logo2 = require('../../../images/dayas3.jpg');
const logo3 = require('../../../images/dayas.jpg');

class SliderDesk extends Component{
    render(){
        const fadeImages = [
             logo,logo1,logo2,logo3
        ];
         
        const fadeProperties = {
          duration: 2000,
          transitionDuration: 500,
          infinite: true,
          indicators: false,
          onChange: (oldIndex, newIndex) => {
            console.log(`fade transition from ${oldIndex} to ${newIndex}`);
          }
        }
         
        /*const Slideshow = () => {*/
          return (
            <div className="slide-container">
              <Fade {...fadeProperties}>
                <div className="each-fade">
                  <div className="image-container">
                    <img src={fadeImages[0]} />
                  </div>
                  <div className='more'>
                      {/* <Col>
                      <h2>Buffing</h2>
                      <Button>Read More</Button>
                      </Col> */}
                      <h2>buffing</h2>
                  </div>
                </div>
                <div className="each-fade">
                  <div className="image-container">
                    <img src={fadeImages[1]} />
                  </div>
                  <div className='more'>
                      <Col>
                      <h2>Blind Dates</h2>
                      <Button>Read More</Button>
                      </Col>
                  </div>
                </div>
                <div className="each-fade">
                  <div className="image-container">
                    <img src={fadeImages[2]} />
                  </div>
                  <div className='more'>
                      <Col>
                      <h2>Blind Dates</h2>
                      <Button>Read More</Button>
                      </Col>
                  </div>
                </div>
                <div className="each-fade">
                  <div className="image-container">
                    <img src={fadeImages[3]} />
                  </div>
                  <div className='more'>
                      <Col>
                      <h2>Blind Dates</h2>
                      <Button>Read More</Button>
                      </Col>
                  </div>
                </div>
              </Fade>
            </div>
          )
        /*}*/
    }
}
export default SliderDesk;
 
