import React,{Component} from 'react';
import {Grid,Cell} from 'react-mdl';
import '../styling/client.css'
import { Row,Col} from 'reactstrap';

class Client extends Component{
    render(){
        return <div className='client-body'>
            <Row>
                <Row >
                    <div style={{width: '20%',paddingLeft: '50px'}}>
                        <img  style={{width: '100px',height: '100px',borderRadius: "50%",}} src={this.props.authorImage}/>
                    </div>
                    <div style={{width: '80%'}}>
                    <Col crossOrigin>
                     <h3>{this.props.authorName}</h3>
                     <p>{this.props.authorDescription}</p>
                    </Col>
                    </div>
                </Row>
            </Row>
        </div>
    }
}
export default Client;