import * as React from 'react';
import { Header, Drawer, Navigation } from 'react-mdl';
import { Row, Col, Nav, Button } from 'reactstrap';
import Slider1 from '../../components/slidingImageShow';
import './new-home.css';
import { Link } from 'react-router-dom';
import grace from '../../images/grace2.jpg';
import Social from '../../components/social';
import TextSlider from '../../components/text-slider';
import FooterMobile from '../components/mobile/footer';

class NewHome extends React.Component{
    render(){
        return <div>
            <div  className='header-mobile'>
                <header className='header'>
                   <Row>
                       <p>Meet New People</p>
                       <Button>Get it</Button>
                   </Row>
                </header>
            </div>
            <Slider1/>
            <div className='author' id='read'>
                <h4>Author's Biography</h4>
                <img className='author-image' src={grace}></img>
                <p>
                My Name is <span style={{color: 'grey'}}>Grace Ephraim Schmidt </span> 
                Born in Dar-es-salaam Tanzania i live in Munich Germany and sometimes Croatia
                I speak Swahili,English,Germany and Croatian I am married since october 2019</p>

                <div className='social'>
                    <Social className='social-media'/>
                </div>
            </div>
            <div className='services'>
                <h4>Our Services</h4>
                <TextSlider></TextSlider>
            </div>
            <div>
                <FooterMobile></FooterMobile>
            </div>
        </div>
    }
}
export default NewHome;
