import React from 'react';
//import { Route } from 'react-router-dom';
import Landing from './landing';
import {Switch,Route, Router} from 'react-router-dom';
import About from './about';
import Respo from './example';
import Resume from './resume';
import Projects from './projects';
import Responsive from '../responsive/Responsive';
import home from '../responsive/mobile/home';
import mobileHome from '../responsive/mobile/mobile-home';
import deskHome from '../responsive/desktop/home-desk';
import history from '../components/history';

const Main=()=>(
    <Router history={history}>
        <switch>
        <Route exact path="/" component={home}/>
        <Route  path="/MobileHome" component={mobileHome}/>
        <Route  path="/DeskHome" component={deskHome}/>
    </switch>
    </Router>
)
export default Main;