import React from "react";
import { render } from "react-dom";
import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";
import Landing from '../pages/landing';
import logo from '../images/dayas.jpg';
import logo1 from '../images/dayas2.jpg';
import logo2 from '../images/dayas3.jpg';
import logo3 from '../images/dayas4.jpg';
import logo4 from '../images/grage.JPG';
import grace from '../images/grace2.jpg';
import history from './history';
import mobileHome from '../responsive/mobile/mobile-home';
// import "normalize.css/normalize.css";
import "./slider-animations.css";
import "./styles.css";

const content = [
  {
    title: "Fluffing",
    description:
      "The unfortunate person who prepares people for actual relationships, but doesnât end up in a relationship him/herself.",
    button: "Read More",
    image: logo,
    user: "Dayas",
    userProfile: grace
  },
  {
    title: "DATERVIEW",
    description:
      "A date that feels more like a job interview. Means we do interviews with a client before setting a blind date",
    button: "Read more",
    image: logo1,
    user: "Dayas",
    userProfile: grace
  },
  {
    title: "UBER RADIUS",
    description:
      "Determining someoneâs desirability based on the distance between where he/she lives and where you live.",
    button: "read more",
    image: logo2,
    user: "Dayas",
    userProfile: grace
  },
  {
    title: "Match Making",
    description:
      "By the previous methods we thereby help you meet your life partner",
    button: "Buy now",
    image: logo3,
    user: "Dayas",
    userProfile: grace
  }
];

const TextSlider = () => (
  <div>

    <Slider className="slider-wrapper">
      {content.map((item, index) => (
        <div
          key={index}
          className="slider-content"
          style={{ background: `url('${item.image}') no-repeat center center` }}
        >
          <div className="inner">
            <h1>{item.title}</h1>
            <p>{item.description}</p>
            <button onClick={()=>history.push('/mobileHome')}>{item.button}</button>
          </div>
          <section>
            <img src={item.userProfile} alt={item.user} />
            <span>
              Posted by <strong>{item.user}</strong>
            </span>
          </section>
        </div>
      ))}
    </Slider>
  </div>
);

render(<TextSlider />, document.getElementById("root"));
export default TextSlider;
