import SimpleImageSlider from "react-simple-image-slider";
import React, {Component} from "react";
import { Button } from "reactstrap";
const logo = require('../images/02.jpg');
const logo1 = require('../images/03.jpg');
const logo2 = require('../images/04.jpg');
const logo3 = require('../images/05.jpg');

class Slider extends React.Component {
    render() {
        const images = [
            { url: logo},
            { url: logo1 },
            { url: logo2 },
            { url: logo3 },
        ];

        return (
            <div>
                <SimpleImageSlider
                    // width={896}
                    // height={504}
                    height={'auto'}
                    width='auto'
                    images={images}
                />
                <Button>Read More</Button>
            </div>
        );
    }
}
export default Slider;