import React, {Component, useState, useEffect} from "react";
import {Grid,Cell,Button} from "react-mdl";
import {container,Row,Col} from "reactstrap";
import Typing from 'react-typing-animation';
import '../components/footer'
import '../styling/landing.css';
import Footer from "../components/footer";
import '../components/slidingImageShow';
import client1 from '../images/avatar.jpg';
import grace from '../images/grage.JPG';
import Slider from "../components/slidingImages";
import Slider1 from "../components/slidingImageShow";
import dayas from '../images/dayas4.jpg';
import logo from '../images/logo.png';
import {faHome, faSquare} from '@fortawesome/free-solid-svg-icons';
import {faFacebook,faInstagram,faAndroid,faAppStoreIos,faIntercom} from '@fortawesome/free-brands-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import Services from "../components/services";
import Client from "../components/client";
import { Container, Link } from 'react-floating-action-button'
import { Responsive } from "../responsive/main";
import Example from '../pages/example';
import ScrollArrow from "../components/scrollTop/scroll-top";

const AddToCart = ({ size }) => {
    if (size=<Responsive displayIn={["Mobile", "Tablet"]}></Responsive>) 
    return <h1 style={{color: 'black'}}>This is mobile</h1>;
    else if(size=<Responsive displayIn={["Laptop"]}></Responsive>)
    return (
      <div className="full tr">
        <button className="product--cart-button">Add to Cart</button>
      </div>
    );
  };
class Landing extends Component{
    constructor(props){
        super(props);
        this.state={
            isMobile: window.innerWidth <420,
        };
        this.updatePredicate = this.updatePredicate.bind(this);
    };
    componentDidMount() {
        this.updatePredicate();
        window.addEventListener("resize", this.updatePredicate);
      }
      componentWillUnmount() {
        window.removeEventListener("resize", this.updatePredicate);
      }
    
      updatePredicate() {
        this.setState({ isMobile: window.innerWidth <420 });
      }
    render(){
        const isMobile = this.state.isMobile;
        return <div className="landing-mother" style={{width:'auto' ,margin:'auto'}}>
             {isMobile ? (
            <div>
                <Row>
                    <FontAwesomeIcon icon={faAndroid}/>
                    <FontAwesomeIcon icon={faAppStoreIos}/>
                </Row>
            </div>
        ) : (
            null
        )}
            <div className='header' style={{paddingTop: '50px'}}>
                <Row>
                    <div className="logo" style={{width: '20%'}}>
                    <img src={logo} style={{width: '100px',paddingLeft: '20px',height: 'auto'}}/>
                    </div>
                    <div style={{width: '60%'}}>
                    <Col>
                    <div>
                    <h3>Dayas-Meet new People,Chat,Socialize</h3>
                    </div>
                     <div>
                     <h3>Free on Google Play</h3>
                     </div>
                    </Col>
                    </div>
                    <div style={{width: '20%',alignSelf: 'left',}}>
                    <Button raised style={{borderColor: '#ff0808',borderRadius: '5px',paddingRight: '10px',background: '#fd1717',color: 'ffffff'}}>Get it</Button>
                    </div>
                </Row>
            </div>
            <Slider1 style={{height: '250px',width: 'auto'}}/>
            {isMobile?(
                <div style={{background: 'red'}}>
                    <h2>Moja</h2>
                </div>
            ):(
                <div></div>
            )}
            <div className='Author' >
                <h1 style={{background: '#fd1717',alignItems: 'center'}}>Founder's Biography</h1>
                <Row>
                    <div style={{width: '50%'}}>
                    <Client
                    authorName="Grace Ephraim Schmidt"
                    authorDescription={'Born in 20/01/1993 in Dar-es-salam,Tanzania \n Lives in Munich,Germany & Croatia Speaks English,Swahili,Germany and Croatian Married Since october 2019 Match Maker'}
                    authorImage={grace}/>
                    </div>
                    <div style={{width: '50%'}}>
                        <img src={grace}></img>
                    </div>
                </Row>
            </div>
            <Grid>
                <Cell className="service-cell" col={12}>
                    {/* <div className='background-image' style={{backgroundImage: "url(" + dayas + ")",}}>
                        <Typing className='style-content'>  
                        <h1 style={{fontSize: '50',color: '#42b4b4'}}>Kutana na Mwenza wako</h1>
                        <Typing.Reset count={2} delay={2500} />
                        <h1>Funga ndoa</h1>
                        <Typing.Backspace count={20} />
                        <Typing.Reset count={3} delay={2500} />
                        </Typing>
                    </div> */}
                     <Container>
            <Link href="#"
                tooltip="Create note link"
                FontAwesomeIcon icon={faFacebook} size='2x'/>
            <Link href="#"
                tooltip="Add user link"
                FontAwesomeIcon icon={faFacebook} size='2x' />
                className="fab-item btn btn-link btn-lg text-white"
            <Button
                tooltip="The big plus button!"
                // icon="fas fa-plus"
                FontAwesomeIcon icon={faFacebook} size='2x'
                rotate={true}
                onClick={() => alert('FAB Rocks!')} />
        </Container>
                </Cell>
            </Grid>
            <div style={{color: 'red',width: '200px'}}>
            <ScrollArrow style={{color: 'black',height: '200px'}}/>
            </div>
            <Footer/>
        </div>
    }
}
export default Landing;